import React from "react";
// Material UI
import {
  AppBar,
  Toolbar,
  Typography,
  IconButton,
  useScrollTrigger,
  makeStyles,
} from "@material-ui/core";
// React Router
import { Link } from "react-router-dom";
// Component
import { RouteData } from "./routes";

const useStyles = makeStyles(() => ({
  root: {
    // backgroundColor: "#cccccc",
    borderRadius: "3px",
  },
  iconButtonLabel: {
    display: "flex",
    flexDirection: "column",
  },
}));

const NavigationBar = () => {
  const trigger = useScrollTrigger({
    threshold: 316,
    disableHysteresis: true,
  });
  const classes = useStyles();
  console.log(trigger);
  return (
    <AppBar
      position="fixed"
      className={classes.root}
      style={{ backgroundColor: trigger === false ? "transparent" : "#cccccc" }}
    >
      <Toolbar>
        <Typography variant="h6" style={{ flexGrow: 1 }}>
          Portfolio
        </Typography>
        {RouteData.map((route) => (
          <IconButton
            key={route.title}
            color="inherit"
            to={route.path}
            component={Link}
            aria-label={route.title}
            classes={{
              label: classes.iconButtonLabel,
            }}
          >
            {route.icon}
            <div style={{ fontSize: "10px" }}>{route.title}</div>
          </IconButton>
        ))}
      </Toolbar>
    </AppBar>
  );
};

export default NavigationBar;
